import React, { Component } from 'react';
import { Divider, Table } from 'antd';
import { setTableSubjectName } from 'dump/data';

class TableSubjectNameSelected extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Ký hiệu',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Tên học phần',
        dataIndex: 'name',
        key: 'name',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }
  render() {
    return (
      <div>
        <Divider>DS môn học đã chọn</Divider>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setTableSubjectName}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableSubjectNameSelected;



