import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import { setLectureHallList, setTypeRooms } from 'dump/data';
import { BtnSearch } from 'components/common/button';
import './style.scss';

@Form.create({ name: 'formClassList' })
class FormInfoCommon extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="Cơ sở:">
              {getFieldDecorator('lectureHall', {})(
                <Select>
                  {setLectureHallList.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Loại phòng:">
              {getFieldDecorator('typeRooms', {})(
                <Select>
                  {setTypeRooms.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label=" ">
              {getFieldDecorator('numberRooms', {})(
                <div>
                  Tổng số phòng: <b>3</b>
                </div>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group" style={{ 'margin-top': '19px' }}>
              <BtnSearch />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



