import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Input, Button, Icon, Form } from 'antd';
import { CartHeadAction } from 'components/common';
import MenuTabTop from './MenuTabTop';
import FormInfoCommon from './FormInfoCommon';
import TableRoomsForAssignSubject from './TableRoomsForAssignSubject';
import TableSubjectNameNotSelected from './TableSubjectNameNotSelected';
import TableSubjectNameSelected from './TableSubjectNameSelected';
import './style.scss';

@Form.create({ name: 'formSearch' })
class ClassRoomsAssignedListPage extends Component {
  state = {};
  render() {
    return (
      <Card>
        <CartHeadAction>
          <MenuTabTop />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormInfoCommon />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableRoomsForAssignSubject />
          </Col>
        </Row>
        <Row>
          <Col span={3}>
            <div>
              <span>Tên học phần:</span>
            </div>
          </Col>
          <Col span={8}>
            <Input />
          </Col>
        </Row>
        <Row>
          <Col span={11}>
            <TableSubjectNameNotSelected />
          </Col>
          <Col span={2}>
            <div className="btnPlusMinus-group">
              <Button style={{ 'margin-bottom': '5px' }}>
                <Icon type="plus" />
              </Button>
              <Button>
                <Icon type="minus" />
              </Button>
            </div>
          </Col>
          <Col span={11}>
            <TableSubjectNameSelected />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassRoomsAssignedListPage />
  </Page>
);



