import React, { Component } from 'react';
import { Divider, Table } from 'antd';
import { setTableRoomsForAssignSubject } from 'dump/data';

class TableRoomsForAssignSubject extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Phòng số',
        dataIndex: 'phongSo',
        key: 'phongSo',
      },
      {
        title: 'Tầng',
        dataIndex: 'tang',
        key: 'tang',
      },
      {
        title: 'Sức chứa',
        dataIndex: 'sucChua',
        key: 'sucChua',
      },
      {
        title: 'Loại phòng',
        dataIndex: 'loaiPhong',
        key: 'loaiPhong',
      },
      {
        title: 'Thiết bị',
        dataIndex: 'thietBi',
        key: 'thietBi',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }
  render() {
    return (
      <div>
        <Divider>Danh sách phòng học</Divider>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setTableRoomsForAssignSubject}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableRoomsForAssignSubject;



